import * as api from "../lib/web3js/index";
import { show, hide } from "react-functional-modal";
import ChainInteraction from "../components/ChainInteraction/ChainInteraction";
import {EVENT_TRANS_BEGIN, EVENT_TRANS_END, MODAL_OPTIONS} from '../utils/Constants'
import Emitter from "../utils/Emitter";
import resolve from "resolve";

class ApiProxy {
  status = 'waiting'
  async request(method,params=[],options = {}){
    const apis = await import('../lib/web3js/index')
    let res = null;
    const {subject,onAccept,onReject,onWarning,onFinish,approveTip,title,content } = options
    if(options.write) {
      params.push({
        onAccept : hash => {
          this.show(subject,'success',approveTip)
          window.setTimeout(() => {
            this.close(subject)
            onAccept && onAccept();
            Emitter.emit(EVENT_TRANS_BEGIN,{title : title,content : content,hash : hash})
          },2000)
          this.getTransactionReceipt(hash,params[0]).then(receipt => {
            if(receipt && options.write && options.includeResponse){
              Emitter.emit(EVENT_TRANS_END,{title : title , content : content,context : {success : true,hash : hash}})
              onFinish && onFinish(receipt)
            }
          })
        },
        onReceipt : () => {
          this.close(subject)
        },
        onReject : () => {
          this.show(subject,'reject',approveTip)
          window.setTimeout(() => this.close(subject),2000)
          onReject && onReject()
        },
        onWarning : () => {
          this.show(subject,'warning',approveTip)
          onWarning && onWarning();
        }
      })
      this.show(subject,'pending',approveTip)

    }
    res = await apis[method].call(this,...params)   
    return this.processResponse(res,options)
  }

  async sendTransaction(method,params=[],options = {}){
    const apis = await import('../lib/web3js/index')
    let res = null;
    const {subject,onAccept,onReject,onWarning,onFinish,approveTip,title,content } = options
    if(options.write) {
      params.push({
        onAccept : hash => {
          this.show(subject,'success',approveTip)
          window.setTimeout(() => {
            this.close(subject)
            onAccept && onAccept();
            Emitter.emit(EVENT_TRANS_BEGIN,{title : title,content : content,hash : hash})
          },2000)
          this.getTransactionReceipt(hash,params.chainId).then(receipt => {
            if(receipt && options.write && options.includeResponse){
              Emitter.emit(EVENT_TRANS_END,{title : title , content : content,context : res })
            }
          })
        },
        onReceipt : () => {
          this.close(subject)
        },
        onReject : () => {
          this.show(subject,'reject',approveTip)
          window.setTimeout(() => this.close(subject),2000)
          onReject && onReject()
        },
        onWarning : () => {
          this.show(subject,'warning',approveTip)
          onWarning && onWarning();
        }
      })
      this.show(subject,'pending',approveTip)

    }
    return await apis[method].call(this,...params)
  }

  async getTransactionReceipt(tx,chainId) {
    const web3 = await api.getWeb3(chainId);
    return new Promise(function _transactionReceipt(resolve, reject) {
      web3.eth.getTransactionReceipt(tx, (error, receipt) => {
        if (error) {
          reject(error);
        } else if (receipt === null) {
          setTimeout(() => _transactionReceipt(resolve, reject), 100);
        } else if (receipt.status === false) {
          reject(receipt);
        } else {
          resolve(receipt);
        }
      });
    })
  }

  syncRequest(method,params = [], options ={}) {
    const res = api[method].call(this,...params)  
    return this.processResponse(res,options)
  }

  close (subject){
    hide(this.getMessageKey(subject))
  }

  getMessageKey(subject){
    return `transaction-box-${subject.split(/\s+/).join('-')}`
  }

  show(subject,status,approveTip){
    const key = this.getMessageKey(subject)
    this.close(key);
    const options = {
      ...MODAL_OPTIONS,
        style : {
          background: "rgba(0, 0, 0, 0.4)" ,
          zIndex : 1001,
        },
        key : key
    }
    show(<ChainInteraction title={subject} status={status} options={{approveTip}} close={() => this.close(subject)}/>,options)
  }

  

  processResponse(res,options){
    if(options.includeResponse){
      if(res && res.response){
        return res
      } 
    } else {
      if(res && res.response){
        return res.response.data
      } 
    }
    return res
  }
}

export default new ApiProxy();